}

body {
  margin-top: 0;
}

.form {
  justify-content: center;
  display: flex;
}

.form, .search--bar {
  width: 100%;
}

.label {
  width: 100%;
  display: block;
  position: relative;
}

.label:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
}

.label__big:after {
  width: 19px;
