  top: 25px;
  left: 26px;
}

.form:first-child {
  margin: 20px auto 0;
}

.big {
  color: var(--text-color);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.big::placeholder {
  font-size: 16px;
